.history{
    max-height: 70vh;
    min-height: 70vh;
    overflow-y: scroll ;
    overflow-x: hidden ;
    width: 30%;
}
li{
    height: 2rem;
    margin-bottom: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    span.method{
        width: 20%;
    }
    span.url{
        width: 70%;
    }

}


.historyPage ul{
    padding: 1rem;
    div{
        display: flex;
        justify-content: space-between;
        width: 100%;
        height: 100%;
    }

    li{
        margin-bottom: 1rem;
        padding: 1rem;
        height: 100%;
        border-radius: 20px;
        display: flex;
        flex-direction: column;
        font-size: 1.1rem;
        font-family: sans-serif;
        box-shadow:0 0 10px 2px rgba(255, 255, 255, 0.678);
        transition: ease 290ms;
    }
    li:hover{
        background-color: black;
        color: white;
        box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.432),0 0 10px 10px orange;
    }
    li:hover span:nth-child(1){
        background-color: gold;
        height: 100%;
        text-align: center;
    }
    .response{
        display: none;
    }
    .GET{
        background-color: lightgreen;
    }
    .POST{
        background-color: lightskyblue;
    }
    .PUT{
        background-color: lightsalmon;
    }
    .DELETE{
        background-color: lightslategrey;
    }
}